<template>
    <div class="container">
        <div class="jumbotron">
            <h1 class="display-5">microCMDB</h1>
            <p>A simple CRUD app to serve as a configuration manager for the monitoring system</p>
            <hr class="my-4">
            <button class="btn btn-outline-dark" @click.prevent="toSignup">SIGNUP&nbsp;&nbsp;<i class="fa fa-arrow-up"></i></button>
            <button class="btn btn-dark" @click.prevent="toLogin">LOGIN&nbsp;&nbsp;<i class="fa fa-arrow-right"></i></button>
        </div>
    </div>
</template>

<script>
export default {
    methods: {
        toLogin() {
            this.$router.push('/login');
        },
        toSignup() {
            this.$router.push('/signup');
        }
    }
}
</script>

<style scoped>
    .jumbotron {
        margin-top: 3em;
    }
</style>



