<template>
  <div class="container">
    <div class="col-lg-8 offset-lg-2">
      <div class="content">
        <h2>Add Host</h2>
        <hr>
        <div class="alert alert-danger" v-if="err">
            {{ err }}
        </div>
        <form>
          <div class="form-row">
            <div class="form-group col-md-4">
              <label>Hostname</label>
              <input type="text" class="form-control" placeholder="MS-FW-01" v-model="host.hostname">
            </div>
            <div class="form-group col-md-4">
              <label>IP Address</label>
              <input type="text" class="form-control" placeholder="192.168.0.10" v-model="host.ipAddress">
            </div>
            <div class="form-group col-md-4">
              <label>Device Type</label>
              <select class="form-control" v-model="host.deviceType">
                <option selected value="">Choose...</option>
                <option value="server">Server</option>
                <option value="network">Network</option>
              </select>
            </div>
          </div>
          <div class="form-row">
            <div class="form-group col-md-12" v-if="host.deviceType == 'server'">
              <label>Operating System</label>
              <input type="text" v-model="host.operatingSystem" class="form-control" placeholder="Microsoft Server 2012 R2">
            </div>
            <div class="form-group col-md-12" v-if="host.deviceType == 'network'">
              <label>SNMP String</label>
              <input type="text" v-model="host.snmpString" class="form-control" placeholder="MS-SNMP-RO">
            </div>
          </div>
          <div class="form-row">
            <div class="form-group col-md-6">
              <label>Location</label>
              <input type="text" v-model="host.location" class="form-control" placeholder="Primary Datacenter">
            </div>
            <div class="form-group col-md-6">
              <label>Contact</label>
              <input type="text" v-model="host.contact" class="form-control" placeholder="it_team@companyemail.com">
            </div>
          </div>
          <button class="btn btn-primary mr-2" @click.prevent="onAdd">Submit</button>
          <button class="btn btn-outline-primary" @click.prevent="onCancel">Cancel</button>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
export default {
    data() {
        return {
            host: {
                hostname: '',
                ipAddress: '',
                deviceType: '',
                operatingSystem: null,
                snmpString: null,
                location: '',
                contact: ''
            },
            err: ''
        }
    },
    methods: {
        onAdd() {
            if (this.host.hostname && this.host.ipAddress && this.host.deviceType && this.host.location && this.host.contact) {
              if (this.host.deviceType == 'server') {
                if (!this.host.operatingSystem) {
                  this.err = `All fields are required`;
                  return;
                }
              } else if (this.host.deviceType == 'network') {
                if (!this.host.snmpString) {
                  this.err = `All fields are required`;
                  return;
                }
              }
              this.$store.dispatch('addHost', { host: this.host })
              .then(response => {
                if (response.data.success) {
                  this.$router.push('/home');
                } else {
                  this.err = response.data.message;
                }
              })
              .catch(err => this.err = err.response.data.message);
            } else {
              this.err = `All fields are required`;
              return;
            }
        },
        onCancel() {
            this.$router.go(-1);
        }
    }
}
</script>

<style scoped>
</style>



