<template>
    <div class="container">
        <div class="content">
            <h2>Hosts
                <small class="ml-1 text-muted">({{ getHosts.length }})</small>
                <button v-if="!reportToggle" class="btn btn-warning ml-3 float-right" data-toggle="modal" data-target="#syncInfoModal">
                    Synchronize Configuration
                </button>
                <div v-else class="dropdown ml-3 float-right">
                    <button class="btn btn-secondary dropdown-toggle mb-4" type="button" data-toggle="dropdown">
                        Choose a Report
                    </button>
                    <div class="dropdown-menu">
                        <button class="dropdown-item" @click.prevent="onFilter('location')">Hosts By Location</button>
                        <button class="dropdown-item" @click.prevent="onFilter('server')">All Server Hosts</button>
                        <button class="dropdown-item" @click.prevent="onFilter('network')">All Network Hosts</button>
                        <button class="dropdown-item text-danger" @click.prevent="onFilter('reset')">Reset</button>
                    </div>
                </div>
                <div class="form-inline float-right" style="margin-right:60px;">
                    <input type="text" class="form-control mr-3" v-model="keyword" style="min-width: 130%;" placeholder="Search by hostname / IP address">
                </div>
            </h2>
            <div class="modal fade" id="syncInfoModal" tabindex="-1" role="dialog" aria-hidden="true">
                    <div class="modal-dialog" role="document">
                        <div class="modal-content">
                        <div class="modal-header">
                            <h5 class="modal-title">
                                <i class="fa fa-info-circle mr-2"></i>Synchronization
                            </h5>
                            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                            <span aria-hidden="true">&times;</span>
                            </button>
                        </div>
                        <div class="modal-body">
                            <p>If executed in a production environment, this button click will synchronize the updated configuration with the monitoring configuration
                             repository.</p>
                        </div>
                        <div class="modal-footer">
                            <button type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
                        </div>
                        </div>
                    </div>
                </div>
            <hr>
            <table class="table table-bordered" v-if="getHosts.length > 0">
                <thead>
                    <tr>
                    <th scope="col">#</th>
                    <th scope="col">Hostname</th>
                    <th scope="col">IP Address</th>
                    <th scope="col">Device Type</th>
                    <th scope="col">Operating System</th>
                    <th scope="col">SNMP String</th>
                    <th scope="col">Location</th>
                    <th scope="col">Contact</th>
                    <th>Edit / Delete</th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="host of getHosts" :key="host._id">
                        <th scope="row">{{ host._id.substr(-7) }}</th>
                        <td>{{ host.hostname }}</td>
                        <td>{{ host.ipAddress }}</td>
                        <td>{{ host.deviceType }}</td>
                        <td>{{ host.operatingSystem || 'N/A' }}</td>
                        <td>{{ host.snmpString || 'N/A' }}</td>
                        <td>{{ host.location }}</td>
                        <td>{{ host.contact }}</td>
                        <td class="text-center">
                            <button class="btn btn-info btn-sm" @click.prevent="onEdit(host._id)"><i class="fa fa-edit"></i></button>
                            <button class="btn btn-danger btn-sm" @click.prevent="onDelete(host._id)"><i class="fa fa-trash"></i></button>
                        </td>
                    </tr>
                </tbody>
            </table>
            
            <h5 v-else class="text-center text-muted">No hosts available. <a href="/add">Add one</a>. Or, your search may have returned 0 results.</h5>
        </div>
    </div>
</template>

<script>
import { EventBus } from '../main.js';
export default {
    created() {
        this.$store.dispatch('fetchHosts')
        .then(response => this.hosts = response.data.data.hosts)
        .catch(err => this.err = err);
        
        EventBus.$on('reportToggle', (payload) => this.reportToggle = payload);
    },
    data() {
        return {
            keyword: '',
            hosts: '',
            err: '',
            reportToggle: false
        }
    },
    computed: {
        getHosts() {
            return this.$store.getters['getHosts'].filter(host => host.hostname.toLowerCase().includes(this.keyword.toLowerCase())
                                                                || host.ipAddress.includes(this.keyword));
        }
    },
    methods: {
        onEdit(id) {
            this.$router.push('/edit/' + id);
        },
        onDelete(id) {
            this.$store.dispatch('deleteHost', { id })
            .then(response => {
                if (response.data.success) {
                    this.$router.go();
                } else {
                    this.err = err.response.message;
                }
            })
            .catch(err => this.err = err.response.message);
        },
        onFilter(filter) {
            if (filter == 'server') {
                let filteredHosts = this.hosts.filter(host => host.deviceType.toLowerCase() == 'server');
                this.$store.commit('setHosts', filteredHosts);
                return;
            } else if (filter == 'network') {
                let filteredHosts = this.hosts.filter(host => host.deviceType.toLowerCase() == 'network');
                this.$store.commit('setHosts', filteredHosts);
                return;
            } else if (filter == 'location') {
                let filteredHosts = this.hosts.sort((a,b) => (a.location > b.location) ? 1 : ((b.location > a.location) ? -1 : 0));
                this.$store.commit('setHosts', filteredHosts);
                return;
            } else if (filter == 'reset') {
                let filteredHosts = this.hosts; 
                this.$store.commit('setHosts', filteredHosts);
            }
        }
    }
}
</script>

<style scoped>
    .container {
        min-width: 1440px;
    }
</style>



