<template>
  <nav class="navbar navbar-expand-lg navbar-dark bg-dark">
    <a class="navbar-brand" href="/home">
      <img src="../../static/gears.svg" class="img-responsive mr-1" width="30px" height="30px">
      microCMDB
    </a>
    <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent"
      aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span class="navbar-toggler-icon"></span>
    </button>

    <div class="collapse navbar-collapse" id="navbarSupportedContent">
      <ul class="navbar-nav mr-auto">
        <li class="nav-item" v-if="getToken" @click.prevent="toAdd">
          <a class="nav-link" href="#"><i class="fas fa-plus mr-2"></i>Add Host</a>
        </li>
        <li class="nav-item" v-if="getToken" @click.prevent="toReports">
          <a class="nav-link" href="#" style=""><i class="fas fa-poll mr-2"></i>Toggle Reporting</a>
        </li>
      </ul>
      <ul class="navbar-nav">
        <li class="nav-item" v-if="!getToken">
          <a class="nav-link" href="/login"><i class="fas fa-lock mr-2"></i>Login</a>
        </li>
        <li class="nav-item" v-if="!getToken">
          <a class="nav-link" href="/signup"><i class="fas fa-user-plus mr-2"></i>Signup</a>
        </li>
        <li class="nav-item" v-if="getToken">
          <button class="nav-link btn btn-outline-default" @click.prevent="logout"><i class="fas fa-sign-out-alt mr-2"></i>Logout</button>
        </li>
      </ul>
    </div>
  </nav>
</template>

<script>
import { EventBus } from '../main.js';

export default {
  data() {
    return {
      reportToggle: false
    }
  },
  computed: {
    getToken() {
      return this.$store.getters['getToken'];
    }
  },
  methods: {
      toAdd() {
        this.$router.push('/add');
      },
      toReports() {
        this.reportToggle = !this.reportToggle;
        EventBus.$emit('reportToggle', this.reportToggle);
      },
      logout() {
        this.$store.dispatch('logout');
        this.$router.replace('/');
      }
  }
}
</script>

<style>

</style>



