<template>
    <div class="content">
        <div class="container">
            <div class="col-lg-6 offset-3">
                <h3>Signup</h3>
                <hr>
                <div v-if="err" class="alert alert-danger">
                    {{ err }}
                </div>
                <form>
                    <div class="form-row">
                        <div class="form-group col-md-12">
                            <label>Username</label>
                            <input type="text" v-model="username" class="form-control" placeholder="Enter username">
                        </div>
                        <div class="form-group col-md-12">
                            <label>Password</label>
                            <input type="password" v-model="password" class="form-control" placeholder="Enter password">
                        </div>
                    </div>
                    <button class="btn btn-primary" @click.prevent="onSignup">Signup</button>
                    <button class="btn btn-outline-primary ml-2" @click.prevent="onCancel">Cancel</button>
                </form>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            username: '',
            password: '',
            err: ''
        }
    },
    methods: {
        onSignup() {
            this.$store.dispatch('signup', { username: this.username, password: this.password })
            .then(response => this.$router.push('/login'))
            .catch(err => this.err = err.response.data.message);
        },
        onCancel() {
            this.$router.push('/');
        }
    }
}
</script>

<style>
    .content {
        margin-top: 3em;
    }
</style>



