import Axios from 'axios';

const axios = Axios.create({
    baseURL: `http://localhost:3000/api/v1`
});


export const login = (user) => axios.post('/users/login', user);

export const signup = (user) => axios.post('/users/signup', user);

export const createHost = (token, host) => axios.post('/hosts/create', host, {
    headers: {
        'Authorization': `Bearer ${ token }`
    }
});

export const updateHost = (token, host, id) => axios.put('/hosts/update/' + id, host, {
    headers: {
        'Authorization': `Bearer ${ token }`
    }
});

export const getOneHost = (token, id) => axios.get('/hosts/get/' + id, { 
    headers: {
        'Authorization': `Bearer ${ token }`
    }
});

export const getHosts = (token) => axios.get('/hosts/get/all', { 
    headers: {
        'Authorization': `Bearer ${ token }`
    }
});

export const deleteHost = (token, id) => axios.delete('/hosts/delete/' + id, {
    headers: {
        'Authorization': `Bearer ${ token }`
    }
});


