import Vue from 'vue';
import Vuex from 'vuex';

import * as axios from '../axios';

Vue.use(Vuex);

export default new Vuex.Store({
    state: {
        hosts: [],
        token: '',
        host: ''
    },
    getters: {
        getHosts(state) {
            return state.hosts;
        },
        getHost(state) {
            return state.host;
        },
        getToken(state) {
            return state.token;
        }
    },
    mutations: {
        setHosts(state, hosts) {
            state.hosts = hosts;
        },
        setHost(state, host) {
            state.host = host;
        },
        setToken(state, token) {
            state.token = token;
        }
    },
    actions: {
        loadToken({ commit }) {
            return new Promise((resolve, reject) => {
                let token = localStorage.getItem('token');
                if (token) {
                    commit('setToken', token);
                    resolve();
                } else {
                    reject();
                }
            });
        },
        login({ commit }, user) {
            return new Promise((resolve, reject) => {
                axios.login(user)
                .then(response => {
                    commit('setToken', response.data.data.token);
                    localStorage.setItem('token', response.data.data.token);
                    resolve();
                })
                .catch(err => {
                    reject(err);
                });
            });
        },
        logout({ commit }) {
            localStorage.clear();
            commit('setToken', '');
        },
        signup({ commit }, user) {
            return new Promise((resolve, reject) => {
                axios.signup(user)
                .then(response => {
                    if (response.data.success) {
                        resolve();
                    } else {
                        reject(response.data.message);
                    }
                })
                .catch(err => {
                    reject(err);
                });
            });
        },
        addHost({ getters }, payload) {
            return new Promise((resolve, reject) => {
                axios.createHost(getters['getToken'], payload.host)
                .then(response => resolve(response))
                .catch(err => reject(err));
            });
        },
        updateHost({ getters }, payload) {
            return new Promise((resolve, reject) => {
                axios.updateHost(getters['getToken'], payload.host, payload.id)
                .then(response => resolve(response))
                .catch(err => reject(err));
            });
        },
        fetchHosts({ commit, getters }) {
            return new Promise((resolve, reject) => {
                axios.getHosts(getters['getToken'])
                .then(response => {
                    commit('setHosts', response.data.data.hosts);
                    resolve(response);
                })
                .catch(err => reject(err));
            });
        },
        deleteHost({ getters }, payload) {
            return new Promise((resolve, reject) => {
                axios.deleteHost(getters['getToken'], payload.id)
                .then(response => resolve(response))
                .catch(err => reject(err));
            });
        },
        fetchHost({ getters }, payload) {
            return new Promise((resolve, reject) => {
                axios.getOneHost(getters['getToken'], payload.id)
                .then(response => resolve(response))
                .catch(err => reject(err));
            });
        }
    }
});


